body {
    /* background: linear-gradient(0deg,#ffe7fa,#e6ffff,#ffffe6); */
}

#main {
    margin: 2em;
    /* text-align: center; */
    margin-top: 5em;
}

button {
    padding: 0.75em;
    border: 1px solid gray;
}

p, button {
    font-size: 1.5em;
}

span {
    font-size: 1em;
}

button:hover:not([disabled]) {
    background: rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

button:focus {
    outline: none;
}

button:active {
    background: rgba(0, 0, 0, 0.2);
}

.pending-tx {
  margin-left: 1em;
  font-size: 1em;
}

.loading {
  margin-left: 1em;
  display: inline-block;
  width: 15px !important;
  height: 15px !important;
}

.loading svg {
    max-width: 15px;
  max-height: 15px;

}




